@import "variables";

.main-banner-section {
  padding: 90px 0 70px;
  @media (max-width: $small-tablet) {
    padding: 90px 25px 90px;
  }
}
.knowledge-section{
  padding: 70px 0 70px;
  @media (max-width: $small-tablet) {
    padding: 70px 25px;
  }
  .items-wrapper {
    display: flex;
    justify-content: flex-start;
    align-items: stretch;
    flex-wrap: wrap;
    gap: 64px 15px;
    padding: 0 100px;
    @media (max-width: $small-tablet) {
      padding: 0;
    }
    .items {
      padding: 30px;
      border-left: 5px solid $white;
      display: flex;
      flex-direction: column;
      gap: 30px;
      width: calc(33.33% - 10px);
      @media (max-width: $large-tablet) {
        width: calc(50% - 10px);
        padding: 25px;
      }
      @media (max-width: $small-phone) {
        width: 100%;
      }
      p {
        color: $black;
        font-family: 'atten_newregular', sans-serif;
        font-size: 16px;
        letter-spacing: 0;
        line-height: 19px;
        text-transform: uppercase;
        span {
          color: $white;
          padding-left: 10px;
        }
      }
      h3 {
        color: $black;
        font-family: 'atten_newregular', sans-serif;
        font-size: 34px;
        letter-spacing: 0;
        line-height: 40px;
        a {
          padding-left: 5px;
          img {
            width: 28px;
            object-fit: contain;
          }
        }
      }
    }
  }
}
.knowledge-mail-section {
  padding: 70px 0 0;
  @media (max-width: $large-tablet) {
    padding: 70px 25px;
  }
  .content-wrapper {
    padding: 0 100px;
    @media (max-width: $large-tablet) {
      padding: 0;
    }
    .image-wrapper {
      position: relative;
      .image {
        width: 65%;
        height: 517px;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;
        background-color: $white;
        position: relative;
        background-image: url("../assets/images/knowledge-king.jpg");
        @media (max-width: $large-tablet) {
          width: 100%;
        }
        p {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 100%;
          height: 100%;
          color: $white;
          z-index: 2;
          position: relative;
          font-family: 'atten_newregular', sans-serif;
          font-size: 41px;
          font-weight: 300;
          letter-spacing: 0;
          line-height: 48px;
          text-align: center;
          @media (max-width: $large-tablet) {
            font-size: 34px;
          }
        }
        &:before {
          content: "";
          position: absolute;
          left: 0;
          top: 0;
          background-color: $black-60;
          width: 100%;
          height: 100%;
        }
      }
      .mail-wrapper {
        width: 45%;
        padding: 60px 78px 71px 71px;
        background-color: $white;
        position: absolute;
        bottom: -50px;
        right: 0px;
        z-index: 2;
        @media (max-width: $large-tablet) {
          width: 100%;
          padding: 25px;
          position: relative;
          bottom: auto;
          right: auto;
        }
        h4 {
          color: $black;
          font-family: 'atten_newregular', sans-serif;
          font-size: 34px;
          font-weight: 300;
          letter-spacing: 0;
          line-height: 40px;
          margin-bottom: 42px;
        }
        .input-wrapper {
          margin-bottom: 24px;
          .checkbox-wrapper {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            margin-top: 10px;
            gap: 10px;
            p {
              color:$black-80;
              font-family: 'atten_newregular', sans-serif;
              font-size: 13px;
              font-weight: 300;
              letter-spacing: 0;
              line-height: 18px;
              a {
                color:$black-80;
                text-decoration: underline;
              }
            }
          }
        }
        .button-wrapper {
          display: flex;
          justify-content: flex-end;
        }
      }
    }
  }
}
