//all common styling will add here


.container {
    @media (max-width: 1200px) {
      max-width: 1440px;
    }
    @media (max-width: $small-tablet) {
      max-width: 100%;
      padding: 0;
    }
  }
.loader-section {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: $yellow;
  z-index: 2;
  position: relative;
  .loader-image {
    width: 110px;
    height: 110px;
    animation: beat 2s infinite alternate;
    transform-origin: center;
    img {
      width: 100%;
    }
  }
}

.btn-blue {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  transition: all .5s ease-out;
  border-radius: 50px;
  background-color: $bule;
  color: $white !important;
  font-family: 'atten_newmedium', sans-serif;
  letter-spacing: 0.1px;
  line-height: 23px;
  border: none;
  &:hover, &:active,  &:focus{
    background-color: $bule;
    color: $white;
  }
}
.btn-h57{
  font-size: 22px;
  height: 57px;
  padding: 0 24px;
}
.btn-h51{
  font-size: 19px;
  height: 57px;
  padding: 0 28px;
}
.btn-h30{
  font-size: 14px;
  height: 30px;
  padding: 0 16px;
}
.cust-input {
  width: 100%;
  border: 1px solid rgb(0 0 0 / 10%);
  height: 60px;
  padding: 10px;
  padding: 16px 22px;
  color: $black-80;
  font-family: 'atten_newregular', sans-serif;
  font-size: 19px;
  letter-spacing: 0;
  line-height: 29px;
  box-shadow: none;
  outline: none;
}
.overflow-hidden{
  overflow: hidden !important;
}
.mb-50{
  margin-bottom: 50px;
}
.mt-50{
  margin-top: 50px;
}
.mb-0{
  margin-bottom: 0;
}
.main-banner-section {
  padding: 90px 0 190px 0;
  @media (max-width: $small-tablet) {
    padding: 90px 25px 90px;
  }
  .container{
    @media (max-width: $small-tablet) {
      padding: 0;
    }
  }
  .banner-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 30px;
    margin-bottom: 75px;
    @media (max-width: $small-tablet) {
      flex-direction: column;
    }
    .banner-content {
      width: 50%;
      padding: 0 50px;
      @media (max-width: $small-tablet) {
        width: 100%;
        padding: 0;
      }
      .logo-wrapper {
        margin-bottom: 25px;
      }

      .text-wrapper {
        margin-bottom: 40px;
        h4 {
          color: $black;
          font-family: 'atten_newmedium', sans-serif;
          font-size: 35px;
          font-weight: 500;
          letter-spacing: 0;
          line-height: 41px;
          text-transform: uppercase;
        }
        p {
          color: $black-80;
          font-family: 'atten_newregular', sans-serif;
          font-size: 19px;
          letter-spacing: 0;
          line-height: 29px;
          margin-top: 37px;
          max-width: 388px;
          @media (max-width: $small-tablet) {
            max-width: 100%;
          }
        }
      }
    }

    .banner-slider {
      width: 50%;
      @media (max-width: $small-tablet) {
        width: 100%;
      }
      .slider-inner {
        .carousal-image {
          width: 100%;
          height: 450px;
          background-size: cover !important;
          background-repeat: no-repeat !important;
          background-position: center center !important;
          position: relative;

          &:before {
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background-color: $black-60;
            z-index: 2;
          }

          &.image-1 {
            background-image: url("../assets/images/banner-image1.jpg");
          }

          &.image-2 {
            background-image: url("../assets/images/banner-image2.jpg");
          }

          &.image-3 {
            background-image: url("../assets/images/banner-image3.jpg");
          }

          .carousel-caption {
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            padding: 0;
            margin: 0;
            right: auto;
            bottom: auto;
            z-index: 2;
            h5 {
              font-family: 'AmazObitaemOstrovV.2', sans-serif;
              font-size: 73px;
              letter-spacing: 0;
              line-height: 93px;
              @media (max-width: $small-tablet) {
                font-size: 33px;
                line-height: 53px;
              }
            }
          }
        }
      }
    }
  }

  .scroll-down {
    display: flex;
    justify-content: center;
    align-items: center;

    a {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      gap: 5px;

      span {
        color: $white;
        font-size: 12px;
        font-weight: 500;
        letter-spacing: 0;
        line-height: 14px;
        text-align: center;
        text-transform: uppercase;
      }
    }

  }
}
