@import "variables";
.topbanner-wrapper {
  width: 100%;
  height: 100vh;
  background-color: $white;    

}
.about-main-banner-section
{
.scroll-down {

    a {

      span {
        color: #9FACAB;
      }
    }

  }
}



.about-text-section {
  padding: 106px 250px 108px 250px;
  background-color: $yellow;
  position: relative;

  .about-text-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 100px;

    .about-content-left,
    .about-content-right {
        width: 50%;
      p {
        color: $black-80;
        font-family: "atten_newbook", sans-serif;
        font-size: 19px;
        padding-bottom: 35px;
        letter-spacing: 0;
        line-height: 29px;
      }
    }

  }
}
.about-imagetext-section {
    padding: 0px 0px 70px;
  .content-wrapper {
    padding: 0 100px;
    .image-wrapper {
      position: relative;
      .image {
        width: 70%;
        height: 517px;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;
        background-color: $white;
        position: relative;
        background-image: url("../assets/images/knowledge-king.jpg");

       
      }
      .mail-wrapper {
        width: 40%;
        padding: 60px 78px 71px 71px;
        background-color: $white;
        position: absolute;
        bottom: -130px;
        right: 0px;
        z-index: 2;
 
     p {
          color: $black-80;
          font-family: "atten_newbook", sans-serif;
         font-size: 19px;
  font-weight: 300;
  letter-spacing: 0;
  line-height: 29px; 
          padding-bottom: 30px;
              
          }  

        .blue-button-wrapper {
          display: flex;
        position: absolute;
            width: 100%;
        bottom: -40px;    
.image-container{
    height: 104px;
    width: 104px;
    display: inline-flex;
                
            }    
            .find-more {
      height: 104px;
    width: 325px;
    background-color: #007299;
    position: absolute;
    bottom: 0px;
    right: 57px;
      a {
        color: #ffffff;
        font-family: "atten_newregular", sans-serif;
        font-size: 22px;
        font-weight: 500;
        letter-spacing: 0;
        line-height: 29px;
        display: block;
        width: 100%;
        height: 100%;
        padding: 25px;
        img {
          position: absolute;
          right: -32px;
          bottom: 15px;
          width: 60px;
        }
      }
    }    
            
            
            
        }
      }
    }
  }
}

@keyframes beat {
  to {
    transform: scale(1.3);
  }
}
