@import "variables";

.knowldge-single {
  padding: 70px 0 0 0;
  .container {
    .items-wrapper {
      flex-wrap: nowrap;
      justify-content: center;
      padding: 0 100px;
      display: flex;

      .items {
        width: 60%;
        flex-direction: column;
        gap: 25px;
        padding: 30px 0px;
        display: flex;
        h3 {
          font-size: 41px;
          font-weight: 300;
          letter-spacing: 0;
          line-height: 48px;
        }

        p {
          color: $black;
          font-family: "atten_newregular", sans-serif;
          font-size: 16px;
          letter-spacing: 0;
          line-height: 19px;
          text-transform: uppercase;
          span {
            color: $white;
            padding-left: 10px;
          }
        }
      }
    }

    .black-area {
      background-color: $black;
      width: 60%;
      margin: 0 auto;
      padding: 60px;
      p {
        color: $white;
        font-size: 19px;
        line-height: 29px;
      }
    }
  }
}

.knowldge-single-text {
  padding: 0 0 70px 0;
  .container {
      
   .outer {
    background-color: $white;
    padding: 50px 0px;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: center;
    .text-wrapper {
      width: 60%;
      h4 {
        font-size: 19px;
        font-weight: bold;
        padding: 15px 0px;
      }

      p {
        font-size: 19px;
        letter-spacing: 0;
        line-height: 29px;
        padding-bottom: 25px;
      }
    }
  }
 }
}

.knowledge-section {
  padding: 70px 0 70px;
  .items-wrapper {
    display: flex;
    justify-content: flex-start;
    align-items: stretch;
    flex-wrap: wrap;
    gap: 64px 15px;
    padding: 0 100px;
    .items {
      padding: 30px;
      border-left: 5px solid $white;
      display: flex;
      flex-direction: column;
      gap: 30px;
      width: calc(33.33% - 10px);
      p {
        color: $black;
        font-family: "atten_newregular", sans-serif;
        font-size: 16px;
        letter-spacing: 0;
        line-height: 19px;
        text-transform: uppercase;
        span {
          color: $white;
          padding-left: 10px;
        }
      }
      h3 {
        color: $black;
        font-family: "atten_newregular", sans-serif;
        font-size: 34px;
        letter-spacing: 0;
        line-height: 40px;
        a {
          padding-left: 5px;
          img {
            width: 28px;
            object-fit: contain;
          }
        }
      }
    }
  }
}
.knowledge-mail-section {
  padding: 70px 0 0;
  .content-wrapper {
    padding: 0 100px;
    .image-wrapper {
      position: relative;
      .image {
        width: 70%;
        height: 517px;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;
        background-color: $white;
        position: relative;
        background-image: url("../assets/images/knowledge-king.jpg");
        p {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 100%;
          height: 100%;
          color: $white;
          z-index: 2;
          position: relative;
          font-family: "atten_newregular", sans-serif;
          font-size: 41px;
          font-weight: 300;
          letter-spacing: 0;
          line-height: 48px;
          text-align: center;
        }
        &:before {
          content: "";
          position: absolute;
          left: 0;
          top: 0;
          background-color: $black-60;
          width: 100%;
          height: 100%;
        }
      }
      .mail-wrapper {
        width: 40%;
        padding: 60px 78px 71px 71px;
        background-color: $white;
        position: absolute;
        bottom: -50px;
        right: 0px;
        z-index: 2;
        h4 {
          color: $black;
          font-family: "atten_newregular", sans-serif;
          font-size: 34px;
          font-weight: 300;
          letter-spacing: 0;
          line-height: 40px;
          margin-bottom: 42px;
        }
        .input-wrapper {
          margin-bottom: 24px;
          .checkbox-wrapper {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            margin-top: 10px;
            gap: 10px;
            p {
              color: $black-80;
              font-family: "atten_newregular", sans-serif;
              font-size: 13px;
              font-weight: 300;
              letter-spacing: 0;
              line-height: 18px;
              a {
                color: $black-80;
                text-decoration: underline;
              }
            }
          }
        }
        .button-wrapper {
          display: flex;
          justify-content: flex-end;
        }
      }
    }
  }
}
