@import "variables";
.header-section {
  padding: 32px 0;
  @media (max-width: $large-tablet) {
    padding: 32px 25px;
  }
  .navbar {
    justify-content: space-between;
    padding: 0;
    .menu-wrapper {
      .navbar-nav {
        gap: 20px;
        align-items: center;
        .nav-item {
          .nav-link {
            position: relative;
            color: $black;
            text-transform: uppercase;
            font-size: 14px;
            font-family: 'atten_newmedium';
            letter-spacing: 0;
            line-height: 17px;
            &:before {
              content: "";
              position: absolute;
              width: 0;
              height: 2px;
              background-color: $white;
              bottom: 0;
              left: 0;
              right: 0;
              margin: 0 auto;
              border-radius: 1px;
              transition: 0.2s ease-in;
            }
          }
          &:hover, &.active {
            .nav-link {
              color: $black-50;

              &:before {
                transition: 0.2s ease-out;
                width: 35px;
              }
            }
          }
          &:last-child {
            .nav-link {
              background-color: $white;
              color: $black;
              border-radius: 20px;
              padding: 10px 20px;

              &:before {
                display: none;
              }
            }
            &:hover, &.active {
              .nav-link {
                color: $black;
                opacity: 0.5;
              }
            }
          }
        }
      }
      .navbar-toggler {
        border: 1px solid $black;;
        padding: 0;
        width: 40px;
        height: 40px;
      }
    }
    &.navbar-light {
      .menu-wrapper {
        .navbar-nav {
          .nav-item {
            .nav-link {
              color: $white;
              &:before {
                background-color: $yellow;
              }
            }
            &:hover, &.active {
              .nav-link {
                color: $white-50;
              }
            }

            &:last-child {
              .nav-link {
                background-color: $yellow;
                color: $black;
              }
            }
          }
        }
        .navbar-toggler {
          border: 1px solid $white;;
        }
      }
    }
  }
}

@keyframes beat{
  to { transform: scale(1.3); }
}
