@import "variables";
@import "testimonials";
@import "footer";
@import "header";
@import "whatwedo";
@import "about";
@import "knowledgesingle";
@import "knowledge";
@import "common";

//all the styling code will go here
html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 3px rgba(0, 0, 0, 0.3);
  border-radius: 6px;
}

::-webkit-scrollbar {
  width: 6px;
}

::-webkit-scrollbar-thumb {
  border-radius: 6px;
  background-image: $gradient-black;
}

body {
  font-family: 'atten_newregular', sans-serif;
  font-size: 14px;
  background-color: $yellow;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

a {
  text-decoration: none;

  &:hover {
    text-decoration: none;
  }

  &:focus {
    outline: none;
    outline-offset: 0;
    text-decoration: none;
  }
}

p, h1, h2, h3, h4, h5 {
  margin: 0;
}

.accounting-section {
  padding: 80px 0px 108px 210px;
  background-color: $white;
  margin-right: 30px;
  @media (max-width: $small-tablet) {
    padding: 50px 25px;
    margin-right: 0;
  }
  .accounting-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 43px;
    @media (max-width: $small-tablet) {
      justify-content: center;
      align-items: center;
    }

    .title-wrapper {
      max-width: 345px;
      @media (max-width: $small-tablet) {
        max-width: 100%;
      }
      h4 {
        color: $black;
        font-family: 'atten_newregular', sans-serif;
        font-size: 34px;
        font-weight: 300;
        letter-spacing: 0;
        line-height: 40px;
      }
    }

    .accounting-content {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      gap: 71px;
      @media (max-width: $small-tablet) {
        flex-direction: column;
      }
      .image-wrapper {
        width: 294px;
        height: 284px;
        @media (max-width: $small-tablet) {
          width: 100%;
        }
        img {
          width: 100%;
          height: 100%;
          object-fit: contain;
        }
      }

      .slider-wrapper {
        width: calc(100% - 365px);
        @media (max-width: $small-tablet) {
          width: 100%;
        }
      }
    }
  }
}

.recruitment-section {
  padding: 127px 0 114px 158px;
  overflow: hidden;
  @media (max-width: $small-tablet) {
    padding: 50px 25px;
  }
  .recruitment-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    @media (max-width: $small-tablet) {
      flex-direction: column;
    }
    .recruitment-content {
      width: 40%;
      @media (max-width: $small-tablet) {
        width: 100%;
      }
      h4 {
        color: $black;
        font-family: 'atten_newregular', sans-serif;
        font-size: 34px;
        font-weight: 300;
        letter-spacing: 0;
        line-height: 40px;
        margin-bottom: 45px;
        max-width: 273px;
      }

      p {
        color: $black-80;
        font-family: 'atten_newregular', sans-serif;
        font-size: 20px;
        font-weight: 300;
        letter-spacing: 0;
        line-height: 28px;
        margin-bottom: 30px;
      }
    }

    .recruitment-image {
      width: 60%;
      display: flex;
      justify-content: flex-end;
      @media (max-width: $small-tablet) {
        flex-direction: column;
        width: 100%;
      }
      .image-wrapper {
        height: 768.67px;
        width: 767.55px;
        position: relative;
        right: -50px;
        @media (max-width: $small-tablet) {
          width: 100%;
          height: 100%;
        }
        img {
          width: 100%;
          height: 100%;
          object-fit: contain;
        }

        .overview-items {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          z-index: 1;


          .item {
            cursor: pointer;
            border-radius: 50px;
            position: absolute;
            width: 46px;
            height: 46px;
            background-color: #000;
            box-shadow: 0px 0px 1px 1px #0000001a;
            animation: pulse-animation 2s infinite;
            opacity: 0;
            z-index: 2;
            @keyframes pulse-animation {
              0% {
                box-shadow: 0 0 0 0px rgba(0, 0, 0, 0.4);
              }
              100% {
                box-shadow: 0 0 0 20px rgba(0, 0, 0, 0);
              }
            }

            &.one {
              top: 75px;
              left: 345px;
              &:hover{
                opacity: 1;
              }
            }

            &.two {
              top: 200px;
              left: 230px;
              &:hover{
                opacity: 1;
              }
            }

            &.three {
              top: 260px;
              left: 115px;
              &:hover{
                opacity: 1;
              }
            }

            &.four {
              bottom: 215px;
              left: 155px;
              &:hover{
                opacity: 1;
              }
            }

            &.five {
              bottom: 25px;
              left: 385px;
              &:hover{
                opacity: 1;
              }
            }

            &.six {
              bottom: 40px;
              right: 145px;
              &:hover{
                opacity: 1;
              }
            }
          }

          .desc {
            opacity: 0;
            width: 367px;
            min-height: 150px;
            transition: opacity .3s ease-in;
            position: absolute;
            background-color: $black;
            padding: 20px 30px;
            border-radius: 10px;
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            flex-direction: column;
            gap: 10px;
            p {
              color: $white;
              font-family: 'atten_newregular', sans-serif;
              font-size: 20px;
              font-weight: 300;
              letter-spacing: 0;
              line-height: 28px;
            }
            a {
              display: flex;
              justify-content: flex-end;
              align-items: flex-end;
              width: 100%;
              color: $yellow;
              font-family: 'atten_newextrabold', sans-serif;
              font-size: 13px;
              font-weight: 800;
              letter-spacing: 1.3px;
              line-height: 28px;
              text-transform: uppercase;
            }

            &.show {
              opacity: 1;
              transition: opacity 0.4s ease-out;
            }

            &.one {
              top: 135px;
              right: 80px;
            }

            &.two {
              top: 250px;
              right: 120px;
            }

            &.three {
              top: 305px;
              left: 165px;

            }

            &.four {
              bottom: 25px;
              left: 205px;
            }

            &.five {
              bottom: 80px;
              left: 20px;

            }

            &.six {
              bottom: 95px;
              right: 190px;

            }
          }
        }
      }
    }

  }
}

.service-wrapper {
  padding: 0 25px 0;
  margin-bottom: -200px;
  @media (max-width: $small-tablet) {
    margin-bottom: 0px;
    padding: 0 25px;
  }
  .image-wrapper {
    .service-image {
      width: 100%;
      height: 100vh;
      background-size: cover !important;
      background-repeat: no-repeat !important;
      background-position: center center !important;
      position: relative;
      padding: 30px;

      &:before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(0deg, $black 0%, rgba(255,255,255,0) 100%);
      }

      &.image-1 {
        background-image: url("../assets/images/services-image1.jpg");
      }

      &.image-2 {
        display: none;
        background-image: url("../assets/images/services-image2.jpg");
      }

      p {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        color: $white;
        font-family: 'atten_newregular', sans-serif;
        font-size: 41px;
        font-weight: 300;
        letter-spacing: -1px;
        line-height: 48px;
        text-align: center;
      }
    }
  }

  .tabs-wrapper {
    margin: 0 auto 50px;
    width: 70%;
    position: relative;
    top: -200px;
    @media (max-width: $small-tablet) {
      width: 100%;
      margin: 50px auto ;
      top: 0;
    }
    .nav-pills {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 32px;
      margin-bottom: 55px;
      @media (max-width: $small-tablet) {
        flex-direction: column;
        width: 100%;
        gap: 15px;
      }
      .nav-item{
        @media (max-width: $small-tablet) {
          width: 100%;
        }
        .nav-link {
          color: $white;
          font-family: 'atten_newregular', sans-serif;
          font-size: 19px;
          font-weight: 500;
          letter-spacing: 0;
          line-height: 23px;
          text-align: center;
          display: flex;
          justify-content: center;
          align-items: center;
          border-radius: 50px;
          height: 60px;
          padding: 0 32px;
          background-color: transparent;
          @media (max-width: $small-tablet) {
            height: 50px;
            width: 100%;
          }
          &.active {
            background-color: $activeTabBG;
            color: $activeTabText;
          }
        }
      }

    }
    .tab-content {
      background-color: $white;
      position: relative;
      padding: 75px 70px 120px;
      @media (max-width: $small-tablet) {
        padding: 25px;
      }
      .tab-pane {
        .tab-text{
          @media (max-width: $small-tablet) {
           margin-bottom: 25px;
          }
          p {
            color: $black-80;
            font-family: 'atten_newregular', sans-serif;
            font-size: 19px;
            font-weight: 300;
            letter-spacing: 0;
            line-height: 29px;
          }
        }
        .find-more {
          position: absolute;
          height: 104px;
          width: 357px;
          background-color: $bule;
          right: -40px;
          bottom: -50px;
          @media (max-width: $small-tablet) {
            right: 0;
            bottom: 0;
            margin: 0 auto;
            width: 100%;
            position: relative;
          }
          a {
            color: #FFFFFF;
            font-family: 'atten_newregular', sans-serif;
            font-size: 22px;
            font-weight: 500;
            letter-spacing: 0;
            line-height: 29px;
            display: block;
            width: 100%;
            height: 100%;
            padding: 25px;
            @media (max-width: $small-tablet) {
              padding: 15px;
              font-size: 18px;
            }
            img {
              position: absolute;
              right: -32px;
              bottom: 10px;
              width: 60px;
              @media (max-width: $small-tablet) {
                bottom: 15px;
                right: 15px;
                width: 34px;
              }
            }
          }
        }
      }
    }
  }
}
