@import "variables";
.topbanner-bg-image {
  background-image: url("../../src/assets/images/media-banner-image.jpg");
  width: 100%;
  height: 100vh;
  background-size: cover !important;
  background-repeat: no-repeat !important;
  background-position: center center !important;
}

.media-banner-section {
  padding: 90px 0 190px 0;

  .media-banner-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 30px;
    margin-bottom: 75px;

    .media-banner-content {
      flex-direction: column;
      align-items: center;
      display: flex;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      .mediabanner-logo {
        width: 55px;
      }

      h3 {
        font-family: "Permanent Marker";
        font-size: 70px;
        line-height: 102px;
        color: $white;
      }
    }
  }
}

.media-creative-section {
  padding: 106px 250px 108px 250px;
  background-color: $white;
  position: relative;

  .media-creative-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 100px;

    .media-creative-content-left,
    .media-creative-content-right {
      p {
        color: $black-80;
        font-family: "atten_newbook", sans-serif;
        font-size: 19px;
        padding-bottom: 35px;
        letter-spacing: 0;
        line-height: 29px;
      }
    }

    .find-more {
      position: absolute;
      height: 104px;
      width: 357px;
      background-color: $bule;
      right: 100px;
      bottom: -10px;
      a {
        color: #ffffff;
        font-family: "atten_newregular", sans-serif;
        font-size: 22px;
        font-weight: 500;
        letter-spacing: 0;
        line-height: 29px;
        display: block;
        width: 100%;
        height: 100%;
        padding: 25px;
        img {
          position: absolute;
          right: -32px;
          bottom: 15px;
          width: 60px;
        }
      }
    }
  }
}

.recruiting-tax-section {
  padding: 106px 250px 0px;
  overflow: hidden;
 background: rgb(255,194,0);
background: -moz-linear-gradient(0deg, rgba(255,194,0,1) 0%, rgba(255,194,0,1) 25%, rgba(0,0,0,1) 25%, rgba(0,0,0,1) 100%);
background: -webkit-linear-gradient(0deg, rgba(255,194,0,1) 0%, rgba(255,194,0,1) 25%, rgba(0,0,0,1) 25%, rgba(0,0,0,1) 100%);
background: linear-gradient(0deg, rgba(255,194,0,1) 0%, rgba(255,194,0,1) 25%, rgba(0,0,0,1) 25%, rgba(0,0,0,1) 100%);
filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ffc200",endColorstr="#000000",GradientType=1);
  h4 {
    color: $white;
    font-family: "atten_newbook", sans-serif;
    font-size: 34px;
    font-weight: 300;
    letter-spacing: 0;
    line-height: 40px;
    margin-bottom: 45px;
    max-width: 450px;
  }
.outer-wrapper {
    background-color: $white;
    padding: 75px;
.logo-wrapper {
  /*     width: 175px;*/
    height: 40px;
    margin-bottom: 30px;
}     
  .recruiting-tax-wrapper {
    display: flex;
    gap: 50px;


    .recruiting-tax-content {
      width: 50%;

      p {
        color: $black-80;
        font-family: "atten_newregular", sans-serif;
        font-size: 18px;
        font-weight: 300;
        letter-spacing: 0;
        line-height: 28px;
        margin-bottom: 30px;
        label {
          font-weight: 500;
          font-style: italic;

          span {
            display: block;
            padding-bottom: 25px;
            b {
              font-weight: 900;
            }
          }
        }
      }
    }
  }
}
  .next-bg {
    width: 100%;
    height: 53vh;
    background-size: cover !important;
    background-repeat: no-repeat !important;
    background-position: center center !important;
    position: relative;
    padding: 30px;

    &:before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(
        0deg,
        $black -100%,
        rgba(255, 255, 255, 0) 100%
      );
    }

    &.image-next {
      background-image: url("../../src/assets/images/next-image.jpg");
    }

    .button-text-wrapper {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      display: flex;
      flex-direction: column;
      align-items: center;

      p {
        color: $white;
        font-family: "atten_newbook", sans-serif;
        font-size: 41px;
        font-weight: 300;
        letter-spacing: -1px;
        line-height: 48px;
        text-align: center;
        padding-bottom: 50px;
      }
    }
  }
}

@keyframes beat {
  to {
    transform: scale(1.3);
  }
}
